import React from "react";
import Layout from "../components/layout";
import Moveforward from "../components/common/move-forward";
import { GatsbyImage, StaticImage, getImage } from "gatsby-plugin-image";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { graphql } from "gatsby";
import { Link } from "gatsby";
import { Card, Row, Col } from "react-bootstrap";

const Blog = (props) => {
  const { pageContext, data } = props;
  console.log("data::", data);
  let title = pageContext?.data?.title;
  console.log(pageContext);

  // let recentKeyId = pageContext?.data?.keyId;
  // let latestData = data?.allDataJson?.edges;
  // latestData.sort((a, b) => b.node.keyId - a.node.keyId);
  // latestData = latestData.filter(
  //   (ele) => Number(ele.node.keyId) !== Number(recentKeyId)
  // );
  // latestData = latestData.slice(0, 3);
  // latestData = {
  //   edges: latestData,
  // };

  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Blog", link: "/blog" },
    { text: title, link: null },
  ];

  const next = pageContext.next?.data
    ? {
        url: `/blog/${pageContext.next.data?.slug}`,
        image: pageContext.next.img?.gatsbyImageData.images?.fallback?.src,
        title: pageContext.next.data.title,
        description: pageContext.next.data.short_description,
        date: pageContext.next.data.blog_created_date,
        author: pageContext.next.data.author_name,
      }
    : null;
  const prev = pageContext.prev?.data
    ? {
        url: `/blog/${pageContext.prev.data?.slug}`,
        image: pageContext.prev?.img?.gatsbyImageData.images?.fallback?.src,
        title: pageContext.prev.data?.title,
        description: pageContext.prev.data?.short_description,
        date: pageContext.prev.data.blog_created_date,
        author: pageContext.prev.data.author_name,
      }
    : null;

  return (
    <>
      <Layout db={pageContext?.firestore?.data?.site?.siteMetadata?.firestore}>
        <Seo
          description={pageContext?.data?.meta_description}
          title={pageContext?.data?.meta_title}
        />
        <BreadCrumb items={breadcrumbLinks} />
        <div className="blog-bg"></div>
        <div className="container">
          <div className="row">
            <div className="col-md-12 col-sm-11 col-11 blog-custom-width">
              <div className="blog-site">
                <h1 className="blog-title text-white">
                  {pageContext?.data?.title}
                </h1>
                <span className="d-flex text-white justify-content-between px-3">
                  <p className="me-3">{pageContext?.data?.blog_created_date}</p>
                  <p className="ms-3">{pageContext?.data?.author_name}</p>
                </span>
                {pageContext?.data?.slug === "unraveling-defi" && (
                  <StaticImage
                    src={"../images/blog-1.png"}
                    alt={"unraveling-defi"}
                  />
                )}
                {pageContext?.data?.slug === "crypto" && (
                  <StaticImage src={"../images/blog-2.png"} alt={"crypto"} />
                )}
                {pageContext?.data?.slug === "blockchain" && (
                  <StaticImage
                    src={"../images/blog-3.png"}
                    alt={"blockchain"}
                  />
                )}
                {pageContext?.data?.slug === "E-commerce" && (
                  <StaticImage
                    src={"../images/blog-4.png"}
                    alt={"E-commerce"}
                  />
                )}
                {pageContext?.data?.slug === "Pharma" && (
                  <StaticImage src={"../images/blog-5.png"} alt={"Pharma"} />
                )}
                {pageContext?.data?.slug === "software-products" && (
                  <StaticImage
                    src={"../images/blog-6.png"}
                    alt={"software-products"}
                  />
                )}
                {pageContext?.data?.slug === "crypto-trading-bot" && (
                  <StaticImage
                    src={"../images/blog-7.png"}
                    alt={"crypto-trading-bot"}
                  />
                )}
                {pageContext?.data?.slug === "Web_Arena_With_The_Graph" && (
                  <StaticImage
                    src={"../images/blog-8.png"}
                    alt={"Web_arena_with_graph"}
                  />
                )}
                <div
                  className="blog-const"
                  dangerouslySetInnerHTML={{
                    __html: pageContext?.data?.description,
                  }}
                ></div>
              </div>
            </div>
          </div>
        </div>
        <div className="custom_margin_top">
          <div className="container">
            <Row className="d-flex justify-content-evenly g-5">
              {next && (
                <Col md={4}>
                  <Link to={next.url}>
                    <Card className="blog_next_card">
                      <Card.Img src={next?.image} />
                      <Card.Body>
                        <Card.Title className="mb-4 text-white fs-1 fw-semibold pt-2 lh-sm position-relative z-3">
                          {next.title.slice(0, 40) + "..."}
                        </Card.Title>
                        <Card.Text className="cta-text text-white mb-2 pt-2 fs-4 position-relative z-3">
                          {next.description.slice(0, 85) + "..."}
                        </Card.Text>
                        {/* <div className="d-flex align-items-center justify-content-between py-4">
                          <p className="text-white position-relative z-3">
                            {next.date}
                          </p>
                          <p className="text-white position-relative z-3">
                            {next.author}
                          </p>
                        </div> */}
                        <div className="d-flex align-items-center justify-content-center next_prev_buttons z-3 position-relative">
                          <svg
                            width="50"
                            height="50"
                            viewBox="0 0 50 50"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <path
                              d="M25 0C20.0555 0 15.222 1.46622 11.1108 4.21326C6.99952 6.96029 3.79521 10.8648 1.90302 15.4329C0.0108317 20.0011 -0.484251 25.0277 0.480379 29.8773C1.44501 34.7268 3.82603 39.1813 7.32234 42.6777C10.8187 46.174 15.2732 48.555 20.1227 49.5196C24.9723 50.4842 29.9989 49.9892 34.5671 48.097C39.1352 46.2048 43.0397 43.0005 45.7867 38.8892C48.5338 34.778 50 29.9445 50 25C49.9922 18.372 47.3558 12.0176 42.6691 7.33093C37.9823 2.6442 31.628 0.00778394 25 0ZM25 47.0588C20.6372 47.0588 16.3723 45.7651 12.7448 43.3412C9.11723 40.9174 6.28989 37.4723 4.62031 33.4415C2.95073 29.4108 2.5139 24.9755 3.36504 20.6965C4.21619 16.4175 6.31708 12.487 9.40207 9.40205C12.487 6.31707 16.4176 4.21617 20.6965 3.36503C24.9755 2.51389 29.4108 2.95072 33.4416 4.6203C37.4723 6.28988 40.9174 9.11722 43.3412 12.7448C45.7651 16.3723 47.0588 20.6372 47.0588 25C47.0523 30.8484 44.7262 36.4553 40.5908 40.5908C36.4554 44.7262 30.8484 47.0523 25 47.0588ZM36.2745 25C36.2745 25.39 36.1196 25.7641 35.8438 26.0399C35.568 26.3156 35.1939 26.4706 34.8039 26.4706H18.7476L24.0784 31.8039C24.2229 31.9385 24.3388 32.1009 24.4192 32.2813C24.4996 32.4617 24.5428 32.6564 24.5463 32.8539C24.5497 33.0513 24.5134 33.2475 24.4395 33.4306C24.3655 33.6137 24.2554 33.78 24.1158 33.9197C23.9761 34.0593 23.8098 34.1694 23.6267 34.2434C23.4436 34.3173 23.2474 34.3537 23.05 34.3502C22.8525 34.3467 22.6578 34.3035 22.4774 34.2231C22.297 34.1427 22.1346 34.0268 22 33.8823L14.1569 26.0392C13.8815 25.7635 13.7268 25.3897 13.7268 25C13.7268 24.6103 13.8815 24.2365 14.1569 23.9608L22 16.1176C22.2788 15.8579 22.6475 15.7165 23.0285 15.7232C23.4095 15.7299 23.773 15.8842 24.0424 16.1537C24.3118 16.4231 24.4662 16.7866 24.4729 17.1676C24.4796 17.5486 24.3382 17.9173 24.0784 18.1961L18.7476 23.5294H34.8039C35.1939 23.5294 35.568 23.6843 35.8438 23.9601C36.1196 24.2359 36.2745 24.61 36.2745 25Z"
                              fill="#FFFFFF"
                            />
                          </svg>
                        </div>
                      </Card.Body>
                      <div className="blog_next_hover_bg"></div>
                    </Card>
                  </Link>
                </Col>
              )}
              {prev && (
                <Col md={4}>
                  <Link to={prev.url}>
                    <Card className="blog_prev_card">
                      <Card.Img src={prev?.image} />
                      <Card.Body>
                        <Card.Title className="mb-4 text-white fw-semibold pt-2 lh-sm fs-1 z-3 position-relative">
                          {prev.title.slice(0, 40) + "..."}
                        </Card.Title>
                        <Card.Text className="cta-text text-white mb-2 pt-2 fs-4 z-3 position-relative">
                          {prev.description.slice(0, 85) + "..."}
                        </Card.Text>
                        {/* <div className="d-flex align-items-center justify-content-between py-4">
                          <p className="text-white z-3">{prev.date}</p>
                          <p className="text-white z-3">{prev.author}</p>
                        </div> */}
                        <div className="d-flex justify-content-center align-items-center next_prev_buttons position-relative z-3">
                          <svg
                            width="50"
                            height="50"
                            viewBox="0 0 50 50"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <path
                              d="M25 0C20.0555 0 15.222 1.46622 11.1108 4.21326C6.99952 6.96029 3.79521 10.8648 1.90302 15.4329C0.0108317 20.0011 -0.484251 25.0277 0.480379 29.8773C1.44501 34.7268 3.82603 39.1813 7.32234 42.6777C10.8187 46.174 15.2732 48.555 20.1227 49.5196C24.9723 50.4842 29.9989 49.9892 34.5671 48.097C39.1352 46.2048 43.0397 43.0005 45.7867 38.8892C48.5338 34.778 50 29.9445 50 25C49.9922 18.372 47.3558 12.0176 42.6691 7.33093C37.9823 2.6442 31.628 0.00778394 25 0ZM25 47.0588C20.6372 47.0588 16.3723 45.7651 12.7448 43.3412C9.11723 40.9174 6.28989 37.4723 4.62031 33.4415C2.95073 29.4108 2.5139 24.9755 3.36504 20.6965C4.21619 16.4175 6.31708 12.487 9.40207 9.40205C12.487 6.31707 16.4176 4.21617 20.6965 3.36503C24.9755 2.51389 29.4108 2.95072 33.4416 4.6203C37.4723 6.28988 40.9174 9.11722 43.3412 12.7448C45.7651 16.3723 47.0588 20.6372 47.0588 25C47.0523 30.8484 44.7262 36.4553 40.5908 40.5908C36.4554 44.7262 30.8484 47.0523 25 47.0588ZM35.8431 23.9608C36.1185 24.2365 36.2732 24.6103 36.2732 25C36.2732 25.3897 36.1185 25.7635 35.8431 26.0392L28 33.8823C27.7212 34.1421 27.3525 34.2835 26.9715 34.2768C26.5905 34.2701 26.227 34.1157 25.9576 33.8463C25.6882 33.5769 25.5338 33.2134 25.5271 32.8324C25.5204 32.4514 25.6618 32.0827 25.9216 31.8039L31.2525 26.4706H15.1961C14.8061 26.4706 14.432 26.3156 14.1562 26.0399C13.8804 25.7641 13.7255 25.39 13.7255 25C13.7255 24.61 13.8804 24.2359 14.1562 23.9601C14.432 23.6843 14.8061 23.5294 15.1961 23.5294H31.2525L25.9216 18.1961C25.6618 17.9173 25.5204 17.5486 25.5271 17.1676C25.5338 16.7866 25.6882 16.4231 25.9576 16.1537C26.227 15.8842 26.5905 15.7299 26.9715 15.7232C27.3525 15.7165 27.7212 15.8579 28 16.1176L35.8431 23.9608Z"
                              fill="#ffffff"
                            />
                          </svg>
                        </div>
                      </Card.Body>
                      <div className="blog_prev_hover_bg "></div>
                    </Card>
                  </Link>
                </Col>
              )}
            </Row>
          </div>
        </div>
        <Moveforward
          db={pageContext?.firestore?.data?.site?.siteMetadata?.firestore}
        />
      </Layout>
    </>
  );
};

export default Blog;

export const query = graphql`
  query MyQuery {
    allDataJson {
      edges {
        node {
          id
          keyId
          slug
          author_name
          blog_created_date
          title
          short_description
          description
          meta_title
          meta_description
          image
          category
          tags
        }
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
