import React, { useState } from "react";
import { Country } from "country-state-city";
import { collection, addDoc } from "firebase/firestore/lite";
import { utils } from "../../core/helper";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";
import Loading from "./loading";
import { Container, Row } from "react-bootstrap";

const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i;

const validationSchema = Yup.object().shape({
  name: Yup.string().label("Name").required(),
  email: Yup.string()
    .matches(emailRegex, "Invalid email address")
    .label("Email")
    .required(),
  country: Yup.string().label("Country").required(),
  mobile: Yup.string().label("Mobile Number or Skype ID").required(),
  content: Yup.string().label("Content").required(),
});

const MoveForward = (props) => {
  console.log("firebase keys:;", props);
  const [loading, setLoading] = useState(false);

  const { db } = props;
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const countries = Country.getAllCountries();

  const handleOnSubmit = async (values) => {
    const { name, email, country, content, mobile } = values;
    const payload = {
      name,
      email,
      country,
      content,
      mobile,
      date: new Date(),
    };
    setLoading(true);
    try {
      // return false
      const clients = collection(db, "clients");
      const resp = await addDoc(clients, payload);
      console.log("resp::", resp);
      utils.showSuccssMsg("We will contact/talk to you soon");
      reset();
      setLoading(false);
    } catch (e) {
      utils.showErrMsg("Error adding document");
      console.log("e::", e);
      setLoading(false);
    }
  };

  return (
    <Container>
      <section className="section-cta" id="cta">
        <div
          className="container p-5 col-md-12 bg-white shadow rounded-5"
          style={{
            background: `linear-gradient(to right bottom, #3c267d, #c27492)`,
          }}
        >
          <div className="row d-flex justify-content-start">
            <div className="col-md-7 col-12 p-3">
              <div>
                <h2 className="text-uppercase text-white">
                  BOOK A QUICK CONSULTATION
                </h2>
                <p className="text-white">
                  Increase efficency, productivity, and business performance
                  with Justtry various products that have been used by tens of
                  thousands of businesses in the word.
                </p>
              </div>

              <form
                onSubmit={handleSubmit(handleOnSubmit)}
                className="cta-form"
              >
                <Row>
                  <div className="col-sm-12 col-12">
                    <label for="full-name">Name</label>
                    <input
                      id="full-name"
                      type="text"
                      placeholder="Enter Your Name"
                      {...register("name")}
                      maxLength={30}
                    />
                    <p className="text-light mb-0">{errors.name?.message}</p>
                  </div>
                  <div className="col-sm-12 col-12">
                    <label for="email">Email address</label>
                    <input
                      id="email"
                      type="email"
                      placeholder="me@example.com"
                      {...register("email")}
                      maxLength={50}
                    />
                    <p className="text-light mb-0">{errors.email?.message}</p>
                  </div>
                </Row>
                <Row>
                  <div className="col-md-12 col-sm-12 col-12">
                    <label for="select-where">Country</label>
                    <select id="select-where" {...register("country")}>
                      <option value="" disabled selected>
                        Select Country
                      </option>
                      {countries.map((ele, ind) => {
                        return (
                          <option key={ind} value={ele.isoCode}>
                            {ele.name}
                          </option>
                        );
                      })}
                    </select>
                    <p className="text-white mb-0">{errors.country?.message}</p>
                  </div>
                  <div className="col-md-12 col-sm-12 col-12">
                    <label for="mobile">Skype / Mobile / Whatsapp</label>
                    <input
                      id="mobile"
                      type="text"
                      placeholder="Skype / Mobile / Whatsapp"
                      {...register("mobile")}
                      maxLength={50}
                    />
                    <p className="text-light mb-0">{errors.mobile?.message}</p>
                  </div>
                </Row>
                <div>
                  <label for="content">Content</label>
                  <textarea
                    id="content"
                    placeholder="Type content here"
                    rows={5}
                    className="input"
                    {...register("content")}
                    maxLength={200}
                  ></textarea>
                  <p className="text-light mb-0">{errors.content?.message}</p>
                </div>
                <button disabled={loading} className="custom-btn btn--form">
                  Submit {loading && <Loading />}
                </button>
              </form>
            </div>

            <div className="col-md-5 col-12 p-4 d-flex flex-column align-items-center justify-content-center">
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3930.2126470840517!2d78.1340045741623!3d9.916240074472167!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3b00c539e6118b11%3A0x6c3891e5d1526c93!2sJusttry%20Technologies!5e0!3m2!1sen!2sin!4v1694181975876!5m2!1sen!2sin"
                className="map-frame"
                allowfullscreen=""
                loading="lazy"
                referrerpolicy="no-referrer-when-downgrade"
              ></iframe>
            </div>
          </div>
        </div>
      </section>
    </Container>
  );
};

export default MoveForward;
